#test {
    color: red;
  }
  
  nav ul {
    margin: 0;
    padding: 0;
    list-style: none;
  }
  nav li {
    display: inline-block;
  }
  nav p {
    display: block;
    padding: 6px 12px;
    text-decoration: none;
  }
  
  .all {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    font-size: 20px;
    justify-content: center;
    align-items: center;
    cursor: default;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    scroll-behavior: smooth;
  }
  
  .dvs {
    background-color: rgb(241, 253, 255);
    box-shadow: var(--shadowsmall);
    border-radius: 20px;
    width: 300px;
    margin: 20px;
    padding: 20px;
    height: 100%;
  }
  
  h1 {
    font-size: 35px;
    text-align: center;
  }
  
  p {
    color: var(--accent);
    font-size: 25px;
    margin-left: 20px;
    margin-top: 20px !important;
    margin-bottom: 5px !important;
  }
  
  .modal-body {
    cursor: text;
    user-select: all;
    display: flex;
    justify-content: space-evenly;
  }
  
  .modal-body span {
    color: green;
  }
  
  #dvsTop {
    background-color: rgb(251, 254, 255);
  }
  
  a {
    text-decoration: none !important;
  }
  
  #customNav {
    background-color: rgba(234, 249, 255, 0.6) !important;
    backdrop-filter: blur(5px) !important;
  }
  
  .active {
    background-color: rgb(177, 233, 255);
    border-radius: 20px;
  }
  
  #hrtLogo {
    box-shadow: var(--shadowsmall);
    width: 100px;
  }
  
  
  
  